import Vue from 'vue'
import Vuex from 'vuex'
import Storage from '../assets/js/storage'
Vue.use(Vuex)
export default new Vuex.Store({
    state: {
        Type: null
    },
    mutations: {
        // 本地存储分类信息
        GetType(state, data) {
            state.Type = data
            Storage.Session.set('Type', JSON.stringify(state.Type))
        }
    },
    actions: {},
    modules: {}
})