import Vue from 'vue'
import VueRouter from 'vue-router'
import { createLogger } from 'vuex'
// 路由懒加载
const Login = () =>
    import ( /*webpackchunkName:login*/ '../components/login')
const Home = () =>
    import ( /*webpackchunkName:home*/ '../components/home')
const User = () =>
    import ( /*webpackchunkName:'User*/ '../components/list/user')
const Article = () =>
    import ( /*webpackchunkName:Article*/ '../components/article/article')
const Addarticle = () =>
    import ( /*webpackchunkName:Addarticle*/ '../components/article/Add_article')
const Personal = () =>
    import ( /*webpackchunkName:P*/ '../components/personal/personal')
const Welcome = () =>
    import ( /*webpackchunkName:W*/ '../components/welcome')
Vue.use(VueRouter)

const routes = [
    { path: '/', redirect: '/login' },
    { path: '/login', name: 'Login', component: Login },
    {
        path: '/home',
        name: 'Home',
        redirect: '/welcome',
        component: Home,
        children: [
            { path: '/welcome', name: 'Welcome', component: Welcome },
            { path: '/user', name: 'User', component: User },
            { path: '/article', name: 'Article', component: Article },
            { path: '/Addarticle', name: 'Addarticle', component: Addarticle },
            { path: '/personal', name: 'Personal', component: Personal }
        ]
    }
]

const router = new VueRouter({
        routes
    })
    // 路由导航卫视
router.beforeEach((to, from, next) => {
    if (to.path == '/login') {
        return next()
    }
    var token = window.sessionStorage.getItem('token')
    if (!token) {
        return next('/login')
    } else {
        next()
    }
})
export default router