const lStorage = window.localStorage
const sStorage = window.sessionStorage
export default {
    Local: {
        get: function(key) {
            return JSON.parse(lStorage.getItem(key) || null)
        },
        set: function(key, val) {
            lStorage.setItem(key, JSON.stringify(val))
        },
        remove: function(key) {
            lStorage.removeItem(key)
        },
        clear: function() {
            lStorage.clear()
        }
    },
    Session: {
        get: function(key) {
            return JSON.parse(sStorage.getItem(key) || null)
        },
        set: function(key, val) {
            sStorage.setItem(key, JSON.stringify(val))
        },
        remove: function(key) {
            sStorage.removeItem(key)
        },
        clear: function(key) {
            sStorage.clear()
        }
    }
}